.links-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    overflow: hidden;
    margin: 20px;
}
.links {
    display: block;
    width: 30%;
    max-width: 226px;
    margin: 10px;
}
.link-image {
    margin: 0 auto;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 4px solid rgba(255, 255, 255, 0.3);
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        object-fit: cover;
    }
}
.link {
    text-align: center;
    h3 {
        color: rgba(0,0,0,0.5);
    }
}
// 添加鼠标事件
.links:hover .link-image>img {
    animation: rotate 2s linear infinite;
}
@keyframes rotate{
	0%{
		transform: rotateZ(0deg);/*从0度开始*/
	}
	100%{
		transform: rotateZ(360deg);/*360度结束*/
	}
}

.links:hover .link>h3 {
    color: #202020;
}