// 定义基础默认
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: "Merr", "Noto", serif;
    font-size: 1rem;
    letter-spacing: 1.5px;
    // background: url(https://laomai.org/wp-content/uploads/2022/04/sea-gc05b59c74_1920.jpg);
    // background-size: cover;
    // background-attachment: fixed;
}
a {
    text-decoration: none;
}
blockquote {
    margin: 1rem auto;
    color: #555555;
    background-color: #f6f6f6;
    border-left: 3px solid #202020;
    position: relative;
    line-height: 1.6;
    padding-left: 0.5rem;
  }
h2, h3, h4, h5, h6, hr {
    margin: 0.5625rem 0;
}
table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    margin: 1.5rem 0;
    font-size: 0.875rem;
}
th {
    display: none;
}
th,
td {
    text-align: left;
    padding: 4px 8px 4px 10px;
    border: 1px solid #999;
}

td {
    vertical-align: top;
}

tr:nth-child(even) {
    background-color: #f6f6f6;
}

::selection {
    background: rgba(0,0,0,0.7);
    color: #fff;
}
@font-face {
    font-family: 'Noto';
    src: url('/font/notoserifsc-regular-webfont.woff2') format('woff2'),
         url('/font/notoserifsc-regular-webfont.woff') format('woff');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Merr';
    src: url('/font/merriweather-regular-webfont.woff2') format('woff2'),
         url('/font/merriweather-regular-webfont.woff') format('woff');
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}