// 自适应
@media screen and (max-width: 820px) {
    .menu-icon {
        display: block;
    }
    .tabs-main {
        padding-top: 0;
        width: 100%;
        position: sticky;
        position: -webkit-sticky;
        z-index: 999;
        background: #fff;
        background: rgba(255, 255, 255, 0.95);
    }
    .tabs a {
        margin-bottom: 0;
        margin-top: 12px;
    }
    .logo-con p {
        text-align: center;
    }
    .menu, .main-header, .post-toc, .main-footer, .line1 {
        display: none;
    }
    .main-content {
        left: 0;
        padding-bottom: 30px;
        height: 100%;
        position: relative;
    }
}
@media screen and (max-width: 700px) {
    .main-container{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 0;
    }
    .content-index {
        width: 100%;
    }
}
// @media screen and (min-width: 821) {
//     #menu-btn {
//         display: none;
//     }
// }
@media screen and (max-height: 1000px) {
    .logo-con {
        display: none;
    }
    .menu-item:nth-child(n+3) {
        display: none;
    }
}
@media screen and (max-height: 863px) {
    .content-index p {
        padding: 40px 2px;
    }
}
@media screen and (max-height: 702px) {
    .content-index {
        width: 100%;
        height: 100%;
        writing-mode: vertical-rl;
    }
}
@media screen and (max-height: 600px) {
    .main-footer {
        display: none;
    }
}
// 这里再写一行是因为.menu-item:nth-child(n+3)的样式出现的位置关系
// 如果要写在一起则需要将@media screen and (max-height: 1000px)放到最前面
@media screen and (max-width: 820px) {
    .menu-item:nth-child(n+3) {
        display: block;
    }
}
@media screen and (max-width: 330px) {
    .content-index {
        width: 100%;
        height: 100%;
        writing-mode: horizontal-tb;
        h1 {
            padding: 0;
        }
        p {
            padding: 0 2rem;
            color: rgba(0, 0, 0, 0.5);
        }
    }
}
@media screen and (max-height: 750px) and (min-width: 820px) {
    .post-toc {
        display: none;
    }
    .menu-item:nth-child(n+3) {
        display: block;
    }
}