.archive-year {
    display: flex;
    padding: 1rem 0;
    flex-wrap: wrap;

    &:not(:last-child) {
        border-bottom: 2px double rgba(168, 164, 164, 0.2);
    }
}

.archive-year-header {
    margin-right: 1rem;
    sup {
        padding: 0 2px;
        color: #3f51b5;
        font-size: 0.8rem;
    }
}

.archive-posts {
    flex-grow: 1;
}

.archive-entry {
    .title {
        display: inline-block;
        margin: 0;
        padding: 0;
    }
    .date {
        display: inline-block;
    }

    a {
        display: flex;
        justify-content: space-between;
        vertical-align: middle;
        padding: 1em 0.5em;
        font-weight: 400;
        border-radius: 4px;
        border-left: 4px solid transparent;
        color: #18272F;
        &:hover {
            background-color: rgba(0,0,0,0.05);
            border-color: #202020;
            color: #202020;
            text-decoration: none;
        }
    }
}
