// 规范浏览器间的样式
@import "normalize";

// base
@import "base";

.main-container {
    position: absolute;
    top: 45px;
    left: 45px;
    right: 45px;
    bottom: 45px;
    box-shadow: 0 0 35px 0 rgba(0,0,0,0.16);
    border-radius: 5px;
    max-width: 1024px;
    margin: auto;
    overflow: hidden;
}
.tabs-main {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    padding-top: 70px;
    width: 200px;
    background: #fff;
    background: rgba(255, 255, 255, 0.75);
}
.menu-icon {
    display: none;
    position: sticky;
    position: -webkit-sticky;
    margin-left: 20px;
    font-size: 32px;
    cursor: pointer;
}
.menu-icon i:hover {
    color: #8f452a;
}
#menu-btn {
    display: none;
}
#menu-btn:checked ~ .menu {
    display: block;
    transition: all 1s ease-in-out;
}
#menu-btn:checked ~ .menu-icon i::before {
    content: "\f00d";
}
.main-header {
    display: flex;
    flex-direction: column;
}
.logo {
    margin: 0 auto;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 4px solid rgba(255, 255, 255, 0.3);
    overflow: hidden;
}
.logo img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}
.logo-con p {
    padding: 10px;
    text-align: justify;
}
.line1 {
    width: 160px;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.5);
    margin: 20px;
    // transition: 0.5s;
}
.line2 {
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.5);
    margin: 20px 0;
    // transition: 0.5s;
}
.post-time {
    position: relative;
    text-align: end;
    right: 20px;
    bottom: 20px;
}
.tabs {
    list-style: none;
}
.tabs a {
    display: block;
    padding: 8px 25px;
    color: #202020;
    border-left: 3px solid rgba(0, 0, 0, 0.4);
    margin-bottom: 12px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 5px;
}
.tabs a:active {
    color: #3f51b5;
    border-left-color: #3f51b5;
}
.tabs a:hover {
    color: #202020;
    border-left-color: #202020;
}
.post-toc {
    display: flex;
    flex-direction: column;
    text-align: end;
}
#TableOfContents {
    max-height: 328px;
    overflow-y: auto;
    // 将滚动条移动到盒子左边
    direction: rtl;
}
#TableOfContents::-webkit-scrollbar {
    width: 4px;
}
#TableOfContents::-webkit-scrollbar-track {
    background-color: transparent;
}
#TableOfContents::-webkit-scrollbar-thumb {
    border-radius: 100px;
    background-image: linear-gradient(180deg, rgba(161, 167, 82, 0.8) 0%, rgba(192, 21, 21, 0.5) 99%);
    box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
}
.post-toc>nav>ol {
    list-style: none;
}
.post-toc>nav>ol>li a {
    display: block;
    padding: 8px;
    color: rgba(0, 0, 0, 0.6);
    border-right: 3px solid rgba(0, 0, 0, 0.4);
    margin-bottom: 8px;
    font-size: 14px;
    // 不换行且超出部分使用省略号
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    direction: ltr;
}
.post-toc>nav>ol>li a:hover {
    color: #202020;
    border-right-color: #202020;
}
.main-footer {
    position: absolute;
    left: 10px;
    bottom: 10px;
    p, span {
        color: rgba(0, 0, 0, 0.4);
    }
    span {
        font-size: 00.875rem;
        span {
            color: #202020;
        }
    }
}
.main-content {
    position: absolute;
    left: 200px;
    top: 0;
    right: 0;
    bottom: 0;
    padding: 7px;
    text-align: justify;
    background: #fff;
    background: rgba(255, 255, 255, 0.93);
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-color: rgba(0,0,0,0.1) rgba(0,0,0,0.4);
    scrollbar-width: thin;
    // 添加此代码会引起页面跳转时有闪屏现象
    // transition: all 0.3s ease;
    img {
        max-width: 100%;
    }
    p {
        margin: 0.5rem 0;
    }
    ul, ol {
        padding-left: 2.2rem;
    }
    ul>li, ol>li {
        padding: 0.25rem 0;
    }
}
.content-header {
    text-align: center;
}
.main-content::-webkit-scrollbar {
    width: 5px;
}
.main-content::-webkit-scrollbar-track, .highlight::-webkit-scrollbar-track {
    background-color: #e4e4e4;
    border-radius: 100px;
}
.main-content::-webkit-scrollbar-thumb, .highlight::-webkit-scrollbar-thumb {
    border-radius: 100px;
    background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.3) 5%, rgba(0, 0, 0, 0.3) 99%);
    box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
}
.highlight::-webkit-scrollbar {
    height: 5px;
}
.content-footer {
    margin: 1.25rem 0;
}
.content-index {
    width: 50%;
    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    writing-mode: vertical-rl;
    h1 {
        padding: 0 10px;
    }
    p {
        padding: 80px 2px;
        color: rgba(0, 0, 0, 0.5);
    }
}
// photoswipe
.pswp img {
    max-width: none;
    border: 10px solid #fefefe;
}

// 代码块
.highlight {
    overflow-x: auto;
    border-radius: 0.375rem;
    background: #282a36;
    margin: 0.5rem 0;
}
code, kbd, samp {
    font-size: 0.875rem;
    background: #282a36;
    color: #f8f8f2;
}
pre {
    padding: 1rem 0.5rem;
}
// 内容页标签
.content-tags>ul {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
    list-style: none;
    li {
        display: inline;
        padding-right: 8px;
    }
}

// 引入a:hover
@import "ahover";

// 引入归档页
@import "archives";

// 引入分页
@import "pagination";

// 自适应
@import "links";

// 自适应
@import "fitted";