.pagination {
    display: block;
    font-size: 90%;
    .pagination-container {
      margin: 0 auto;
      padding: 1.875rem 0 1.375rem;
      text-align: right;
      li {
        list-style: none;
        display: inline-block;
        margin-right: 12px;
        padding: 0.1rem 0;
        a {
          display: inline-block;
          padding: 6px 12px;
          white-space: nowrap;
          outline: 0;
          &:visited {
            color: inherit;
          }
        }
        &.current {
          outline: 1px solid #0078d4;
        }
        &.disabled,
        &.current {
          a {
            pointer-events: none;
            cursor: default;
            text-decoration: none;
          }
        }
      }
    }
  }