  .ahover a {
    color: #18272F;
    position: relative;
  }
  
  .ahover a::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    background-color: #202020;
    bottom: -2px;
    left: 0;
    transform-origin: right;
    transform: scaleX(0);
    transition: transform .3s ease-in-out;
  }
  
  .ahover a:hover::before {
    transform-origin: left;
    transform: scaleX(1);
  }